// Modules
import React from 'react';
import { observer, Provider } from 'mobx-react';
import { observable, computed, action } from 'mobx'
import Slider from 'rc-slider';
import 'rc-slider/assets/index.css';
import NumberFormat from 'react-number-format';

// Function for formatting dollars and ,
function numberFormatter(v) {
    return <NumberFormat
        value={v}
        displayType={'text'}
        thousandSeparator={true}
        prefix={'$'} />
}

// Media
import './App.scss';

// Gobal Store
class AppStore {

    @observable payroll = 850000;

    calc = new Calc(this, 850000)
    newCalc = new Calc(this, 950000)
    difference = new Difference(this)

    @action setPayroll = e => {
        if (e.target) {
            this.payroll = e.target.value;
        } else {
            document.getElementById("payroll").blur();
            this.payroll = e;
        }
    }
    @action setThres = (e) => {
        let min = parseInt(e.target.value,10);
        this.newCalc.updateThres(min);
    }
}

// Calculator
class Calc {

    @observable minimum = 850000;

    constructor(store, minimum, threshold) {
        this.store = store
        this.minimum = minimum || 850000
        this.maximum = 7500000
        this.threshold = threshold || (this.minimum / (this.maximum - this.minimum))
    }

    @action updateThres = min => {
        this.minimum = min
        this.threshold = (this.minimum / (this.maximum - this.minimum))
    }

    @computed get taxRate() {
        if (((this.TaxPayable / this.store.payroll) * 100) > 5.5)
            return 5.5
        return parseFloat((this.TaxPayable / this.store.payroll) * 100).toFixed(2)
    }

    @computed get ExceptionThreshold() {
        if (this.store.payroll < this.minimum) {
            return this.minimum
        }
        if (this.store.payroll > this.maximum) {
            return 0
        }
        return Math.floor(this.minimum - ((this.store.payroll - this.minimum) * this.threshold))
    }

    @computed get ActualPayroll() {
        if (this.store.payroll < this.minimum) {
            return 0
        }
        return Math.floor(this.store.payroll - this.ExceptionThreshold)
    }

    @computed get TaxPayable() {
        return Math.floor(this.ActualPayroll * 0.055);
    }
}

// Savinges / Difference Calculations
class Difference {

    constructor(store) {
        this.store = store
    }

    @computed get ExceptionThreshold() {
        if (this.store.payroll > 7500000)
        	return 0
        return this.store.newCalc.ExceptionThreshold - this.store.calc.ExceptionThreshold;
    }

    @computed get ActualPayroll() {
        if (this.store.payroll > 7500000)
            return 0
        return this.store.calc.ActualPayroll - this.store.newCalc.ActualPayroll;
    }

    @computed get TaxPayable() {
        if (this.store.payroll > 7500000)
            return 0
        return this.store.calc.TaxPayable - this.store.newCalc.TaxPayable
    }

    @computed get taxRate() {
        return parseFloat(this.store.calc.taxRate - this.store.newCalc.taxRate).toFixed(2)
    }

}

// Initiate Store
const store = new AppStore();

// Prepare the App
const App = () =>
    <Provider store={store}>
        <div className="App">
            <Header />
            <Body />
        </div>
    </Provider>
            

const Header = () =>
    <div className="App-header">
        <h2 className="App-title">WA Payroll Tax Calculator</h2>
        <div className="App-subtitle">
            Find out how much tax you are required to pay under the current 2016 standards.</div>
    </div>

const Body = observer((["store"]), () => {

    // Initiate Slider with some mark points    
    const marks = {
        "0": <small>$0</small>,
        '850000': <small>$850,000</small>,
        '2500000': <small>$2,500,000</small>,
        '5000000': <small>$5,000,000</small>,
        '7500000': <small>$7,500,000</small>,
    }

    return (
        <div className="App-body" >
            <div className="desc">
                <h3>
                    1. Enter your annual payroll:
                </h3>
                <small>
                    The total annual amount for combined salaries for all employees.
				</small>
            </div>
            <div className="input-holder">
                <input
                    id="payroll"
                    value={store.payroll}
                    min={0}
                    onChange={store.setPayroll} />
                
                <div style={{ margin: 2, marginTop: 10 }}>
                    <Slider
                        min={0}
                        max={store.calc.maximum}
                        marks={marks}
                        value={store.payroll}
                        step={50000}
                        tipFormatter={numberFormatter}
                        onChange={store.setPayroll} />
                </div>
                <br />    
                <div>
                    Threshold Proposed:
                    <input
                        type="text"
                        defaultValue={store.newCalc.minimum}
                        onChange={store.setThres} />
                </div>
            </div>
            <hr />
            <Payroll />
        </div>
    )
})

const Payroll = observer((["store"]), () =>
    <table className="payroll">
        <thead>
            <tr>
                <th>Title</th>
                <th>Currently</th>
                <th>Proposed</th>
                <th>Difference <small>(how much you save)</small></th>

            </tr>
        </thead>
        <tbody>
            <tr>
                <td>Your Payroll:</td>
                <td>
                    <NumberFormat value={store.payroll} displayType={'text'} thousandSeparator={true} prefix={'$'} /></td>
                <td></td>
                <td></td>

            </tr>
            <tr>
                <td>Exemption Threshold:</td>
                <td>
                    {store.calc.ExceptionThreshold === 0 ? "$0" : null}
                    <NumberFormat value={store.calc.ExceptionThreshold} displayType={'text'} thousandSeparator={true} prefix={'$'} />
                </td>
                <td>
                    {store.newCalc.ExceptionThreshold === 0 ? "$0" : null}
                    <NumberFormat value={store.newCalc.ExceptionThreshold} displayType={'text'} thousandSeparator={true} prefix={'$'} /></td>
                <td>

                </td>

            </tr>
            <tr>
                <td>Actual Payroll:</td>
                <td>
                    {store.calc.ActualPayroll === 0 ? "$0" : null}
                    <NumberFormat value={store.calc.ActualPayroll} displayType={'text'} thousandSeparator={true} prefix={'$'} />
                </td>
                <td>
                    {store.newCalc.ActualPayroll === 0 ? "$0" : null}
                    <NumberFormat value={store.newCalc.ActualPayroll} displayType={'text'} thousandSeparator={true} prefix={'$'} /></td>
                <td>
                    {store.difference.ActualPayroll === 0 ? "$0" : null}
                    <NumberFormat value={store.difference.ActualPayroll} displayType={'text'} thousandSeparator={true} prefix={'$'} /></td>

            </tr>
            <tr>
                <td>Tax Payable:</td>
                <td>
                    {store.calc.TaxPayable === 0 ? "$0" : null}
                    <NumberFormat value={store.calc.TaxPayable} displayType={'text'} thousandSeparator={true} prefix={'$'} />
                </td>
                <td>
                    {store.newCalc.TaxPayable === 0 ? "$0" : null}
                    <NumberFormat value={store.newCalc.TaxPayable} displayType={'text'} thousandSeparator={true} prefix={'$'} /></td>
                <td>
                    {store.difference.TaxPayable === 0 ? "$0" : null}
                    <NumberFormat value={store.difference.TaxPayable} displayType={'text'} thousandSeparator={true} prefix={'$'} /></td>

            </tr>
            <tr>
                <td>Tax Rate:</td>
                <td>{store.calc.taxRate}%</td>
                <td>{store.newCalc.taxRate}%</td>
                <td>{store.difference.taxRate}%</td>
            </tr>

        </tbody>
    </table>
)

export default App;



